'use client';

import { SVGConfig } from '@/app/page';
import { useMemo, useEffect, useRef } from 'react';
import { generateInlineSVG } from './CodeModal';

interface PreviewPanelProps {
  svgContent: string;
  config: SVGConfig;
  isDarkMode: boolean;
}

export function PreviewPanel({ svgContent, config, isDarkMode }: PreviewPanelProps) {
  const svgContainerRef = useRef<HTMLDivElement>(null);

  // generateInlineSVG    
  const inlineSVGResult = useMemo(() => {
    if (!svgContent) return '';
    console.log('PreviewPanel config changed:', config);
    const result = generateInlineSVG(svgContent, config, 'preview');
    console.log('Generated HTML result:', result.substring(0, 800));
    return result;
  }, [svgContent, config]);

  // DOM     
  useEffect(() => {
    console.log('useEffect triggered, updating DOM');
    if (svgContainerRef.current && inlineSVGResult) {
      console.log('Actually updating innerHTML');
      svgContainerRef.current.innerHTML = inlineSVGResult;
      
      //  DOM 
      console.log('DOM after update:', svgContainerRef.current.innerHTML.substring(0, 500));
      
      // strokeWidth   
      const svgElements = svgContainerRef.current.querySelectorAll('svg *[stroke-width], svg *[strokeWidth]');
      console.log('Elements with stroke-width:', svgElements);
      svgElements.forEach((el, index) => {
        console.log(`Element ${index}:`, el.getAttribute('stroke-width') || el.getAttribute('strokeWidth'));
      });
    } else {
      console.log('svgContainerRef or inlineSVGResult missing:', {
        hasRef: !!svgContainerRef.current,
        hasResult: !!inlineSVGResult
      });
    }
  }, [inlineSVGResult]);

  return (
    <div className="h-full flex flex-col overflow-y-auto">
      {/* generateInlineSVG  CSS      */}
      <div className="flex items-center justify-between mb-6">
        <h2 className={`text-xl font-semibold ${isDarkMode ? 'text-white' : 'text-gray-900'}`}>Preview</h2>
        <div className={`text-sm ${isDarkMode ? 'text-gray-400' : 'text-gray-600'}`}>
          {config.size}px  {config.size}px
        </div>
      </div>

      <div className="flex-1 flex items-center justify-center p-8">
        <div className="relative">
          {/* Fixed size background container */}
          <div 
            className={`relative ${isDarkMode ? 'bg-gray-900/30' : 'bg-gray-100/50'} rounded-lg w-[800px] h-[800px] flex items-center justify-center overflow-hidden`}
            data-preview-svg
          >
            {/* Grid Background */}
            <div 
              className="absolute inset-0 opacity-10 pointer-events-none"
              style={{
                backgroundImage: `
                  linear-gradient(${isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)'} 1px, transparent 1px),
                  linear-gradient(90deg, ${isDarkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)'} 1px, transparent 1px)
                `,
                backgroundSize: '20px 20px',
                backgroundPosition: '0 0, 0 0'
              }}
            />
            
            {/* React    */}
            <div 
              style={{
                width: `${config.size}px`,
                height: `${config.size}px`,
                transform: `rotate(${config.rotation}deg)`,
                opacity: config.opacity,
                transition: 'all 1.5s ease-in-out',
                animation: config.animation !== 'none' ? `${config.animation} 2s infinite` : undefined,
              }}
            >
              <div 
                dangerouslySetInnerHTML={{ __html: processedSVG || '' }}
              />
            </div>
          </div>
        </div>
      </div>

      {/* Config Summary */}
      <div className={`mt-6 p-4 ${isDarkMode ? 'bg-gray-900/50' : 'bg-gray-100/50'} rounded-lg`}>
        <h3 className={`text-sm font-medium ${isDarkMode ? 'text-white' : 'text-gray-900'} mb-2`}>Current Settings</h3>
        <div className={`grid grid-cols-2 gap-2 text-xs ${isDarkMode ? 'text-gray-400' : 'text-gray-600'}`}>
          <div>Stroke: {config.color}</div>
          <div>Fill: {config.fillColor}</div>
          <div>Size: {config.size}px</div>
          <div>Rotation: {config.rotation}</div>
          <div>Opacity: {Math.round(config.opacity * 100)}%</div>
          <div>Animation: {config.animation}</div>
          <div>Hover: {config.hoverEffect}</div>
        </div>
      </div>
    </div>
  );
}